
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
import $ from 'jquery'
export default {
name: "login",
components:{XInput,Group,XButton},
data(){
return{
value2:'',
value1:'',
}
},

methods:{
getCode(){


if(this.value1===""){
alert("手机号不能为空")
}
else {
axios.get("http://e.shushangsoft.com/customerLoginController.do?method=sendYzm&type=1&loginType=2&phone="+this.value1).then((res)=>{
alert("已发送")
})
}
},
login(){
axios.get("http://e.shushangsoft.com/customerLoginController.do?method=login&phone="+this.value1+"&type=1&code="+this.value2+"&loginType=2").then((res)=>{

if(res.data.ret==="200"){
this.$router.replace({
path: '/home',query:{}
});
this.$store.commit('updateIsLogin', true)
localStorage.setItem("login","true");
}else {
alert(res.data.msg)
}
})
}
}
}



